<template>
  <!-- 普通的txt文件预览和编辑 语法高亮 -->
  <el-dialog :visible.sync="dialog">
    <pre v-highlightjs="text"><code class="Java"></code></pre>
    <!-- <pre v-highlight="text" style="margin:50px"><code></code></pre> -->
  </el-dialog>
</template>

<script>
  export default {
    data() {
      return {
        dialog: true
      }
    },
    mounted() {

    },
    props: ["text"],

    methods: {}
  }
</script>

<style>
  code {
    text-align: left;
  }
</style>



