<template>
  <div>


    <div class="status_bar">
      <el-menu :router="true" :default-active="name" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="/storage">全部</el-menu-item>
        <el-menu-item index="/storage/Music">
          音乐
        </el-menu-item>
        <el-menu-item index="/storage/Video">
          视频
        </el-menu-item>
        <el-menu-item index="/storage/Document">
          文档
        </el-menu-item>
        <el-menu-item index="/storage/Picture">
          图片
        </el-menu-item>
        <el-menu-item index="/storage/Other">
          其他
        </el-menu-item>
        <el-menu-item index="/storage/TrashBin">
          回收站
        </el-menu-item>
        <el-submenu>
          <template slot="title">插件</template>
          <el-menu-item index="/storage/ShareFolderGroup">共享文件夹</el-menu-item>
          <el-menu-item index="/storage/BtDownload">下载列表</el-menu-item>
          <el-menu-item index="/storage/Oss">对象存储</el-menu-item>
        </el-submenu>
        <el-menu-item style="float: right;padding-bottom: 0px;">
          <el-dropdown>
            <span class="el-dropdown-link">
              <i class="el-icon-time"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item icon="el-icon-plus">黄金糕</el-dropdown-item>
              <el-dropdown-item icon="el-icon-circle-plus">狮子头</el-dropdown-item>
              <el-dropdown-item icon="el-icon-circle-plus-outline">螺蛳粉</el-dropdown-item>
              <el-dropdown-item icon="el-icon-check">双皮奶</el-dropdown-item>
              <el-dropdown-item icon="el-icon-circle-check">蚵仔煎</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-menu-item>
        <el-submenu style="float: right;padding: 0;margin: 0,5px,0,5px" index="User">
          <template slot="title">{{userName}}</template>
          <el-menu-item><i class="el-icon-user"></i><span>用户资料</span></el-menu-item>
          <el-menu-item index="/storage/admin"><i class="el-icon-setting"></i><span>设置</span></el-menu-item>
          <el-menu-item v-if="isLogin" index="/User/logout"><i class="el-icon-s-flag"></i><span>退出</span>
          </el-menu-item>
        </el-submenu>

      </el-menu>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        userName: '',
        rememberme: '',
        name: window.location.pathname,
        today: new Date().toLocaleDateString(),
        items: [],
        isLogin: false
      }
    },
    mounted() {
      this.getCookie();
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      get() {

      },
      getCookie: function() {
        if (document.cookie.length > 0) {
          var arr = document.cookie.split('; '); //这里显示的格式需要切割一下自己可输出看下
          for (var i = 0; i < arr.length; i++) {
            var arr2 = arr[i].split('='); //再次切割
            if (arr2[0] == 'nickName') {
              this.userName = (arr2[1]); //cookie中的userName
              this.isLogin = true;
              return;
            }
          }
          this.$router.push("/User/login");
        } else {
          this.$router.push("/User/login");
        }
      }
    }
  }
</script>

<style>
  .status_bar {
    display: inline-block;
    width: 100%;
  }

  .status_bar i {
    margin: 0.3125rem;
  }
</style>



