import Vue from 'vue'
import Router from 'vue-router'
import login from '@/views/Login'
import register from '@/views/Register'
import main from '@/views/index'
import downloadList from '@/views/downloadList.vue'
import FileVersionManger from '@/views/FileVersionManger.vue'
import Setting from '@/views/Setting.vue'
import Schedule from '@/views/Schedule.vue'
import video from '@/views/video.vue'

import Oss from '@/views/Oss.vue'
import Other from '@/views/Other.vue'
import Picture from '@/views/Picture.vue'
import Document from '@/views/Doc.vue'
import Music from '@/views/Music.vue'
import Video from '@/views/VideoList.vue'
import Folder from '@/views/ShareFolderList.vue'

import TrashBin from '@/views/trashbin.vue'

import ShareFolder from '@/views/MyShareGroup.vue'
Vue.use(Router)
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

export default new Router({
  mode: "history",
  routes: [{
      path: '/',
      name: 'main',
      component: main,
      meta: {
        hidden: true,
        title: "我的网盘"
      }
    }, {
      path: '/storage',
      name: 'main',
      component: main,
      meta: {
        hidden: true,
        title: "我的网盘"
      }
    },
    {
      path: '/storage/Music',
      name: 'Music',
      component: Music,
      meta: {
        hidden: true,
        title: "音乐"
      }
    },
    {
      path: '/storage/Video',
      name: 'Video',
      component: Video,
      meta: {
        hidden: true,
        title: "视频"
      }
    },
    {
      path: '/storage/Document',
      name: 'Document',
      component: Document,
      meta: {
        hidden: true,
        title: "文档"
      }
    },{
      path: '/storage/Other',
      name: 'Other',
      component: Other,
      meta: {
        hidden: true,
        title: "其他"
      }
    },
    {
      path: '/storage/Picture',
      name: 'Picture',
      component: Picture,
      meta: {
        hidden: true,
        title: "图片"
      }
    },{
      path:'/storage/TrashBin',
      name:'TrashBin',
      component:TrashBin,
      meta:{
        hidden:true,
        title:"回收站"
      }
    },
    {
      path: '/storage/ShareFolder',
      name: 'Folder',
      component: Folder,
      meta: {
        hidden: true,
        title: "共享文件夹"
      }
    },
    {
      path: '/User/login',
      name: login,
      component: login,
      meta: {
        hidden: true,
        title: "登陆"
      }
    },
    {
      path: '/register',
      name: register,
      component: register,
      meta: {
        hidden: true,
        title: "注册"
      }
    },

    {
      path: '/video',
      name: 'video',
      component: video
    },
    {
      path: '/storage/BtDownload',
      name: 'downloadList',
      component: downloadList
    },
    {
      path: '/storage/FileVersionManger',
      name: 'FileVersionManger',
      component: FileVersionManger,
      meta: {
        hidden: true,
        title: "版本控制"
      }
    },
    {
      path: '/storage/Oss',
      name: 'Oss',
      component: Oss,
      meta: {
        hidden: true,
        title: "对象存储"
      }
    },
    {
      path: '/storage/admin',
      name: 'Setting',
      component: Setting,
      meta: {
        hidden: true,
        title: "设置"
      }
    }, {
      path: '/storage/Schedule',
      name: 'Schedule',
      component: Schedule,
      meta: {
        hidden: true,
        title: "定时任务"
      }
    },
     {
      path: '/storage/ShareFolderGroup',
      name: 'ShareFolder',
      component: ShareFolder,
      meta: {
        hidden: true,
        title: "共享文件夹组"
      }
    }
  ]
})



