<template>
  <div>
    <bannerBar></bannerBar>
    <el-tabs v-model="activeName">
      <el-tab-pane label="我的文件夹" name="Share">
        <el-table :data="shareList">
          <el-table-column label="文件夹名" prop="name"><template slot-scope="scope">
              <a style="color: #000000;margin-left: 5px;" @click="go(scope)">{{scope.row.name}}</a>
            </template></el-table-column>
          <el-table-column label="原有用户名" prop="originUserName"></el-table-column>
          <el-table-column label="授予用户" prop="shareUserName"></el-table-column>
          <el-table-column><template slot-scope="scope">
              <el-button size="mini" @click="openShareDialog(scope)">权限控制</el-button>
              <!-- <el-button size="mini">上传记录</el-button> 这个直接放到文件夹里-->
              <el-button size="mini" type="danger">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="共享给我的文件夹" name="Shared">
        <el-table :data="sharedList">
          <el-table-column label="文件夹名" prop="name"></el-table-column>
          <el-table-column label="原有用户名" prop="originUserName"></el-table-column>
          <el-table-column label="授予用户" prop="shareUserName"></el-table-column>
          <el-table-column><template slot-scope="scope">
              <el-button size="mini" @click="openSharedDialog(scope)">权限</el-button>
              <el-button size="mini" type="danger">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
    <el-dialog :visible.sync="shared_permission.show">
      <el-checkbox v-for="folder in allPermission" v-model="shared_permission.permission" :label="folder.id" border>{{folder.permissionName}}</el-checkbox>
    </el-dialog>
    <el-dialog :visible.sync="permission.show">
      <el-checkbox v-for="folder in allPermission" v-model="permission.permission" :label="folder.id" border>{{folder.permissionName}}</el-checkbox>
    </el-dialog>
  </div>
</template>

<script>
  import bannerBar from "../components/banner_bar.vue"
  export default {
    components: {
      bannerBar
    },
    data() {
      return {
        activeName: 'Share',
        shareList: [],
        sharedList: [],
        shared_permission:{
          show:false,
          permission:[]
        },
        permission:{
          show:false,
          permission:[],
        },
        allPermission:[]
      }
    },
    mounted() {
      this.getMyShare();
      this.getShareToMe();
      this.$axios.get("/api/disk/folderPermissions").then(response=>{
        this.allPermission=response.data.result;
      })
    },
    methods: {
      getMyShare() {
        this.$axios.get("/api/disk/MyShareFolder").then(response => {
          this.shareList = response.data.result;
        })
      },
      getShareToMe() {
        this.$axios.get("/api/disk/ShareToMe").then(response => {
          this.shareList = response.data.result;
        })
      },
      go(scope){
        this.$router.push({path: '/storage',query: {parentName: scope.row.name,}});
      },
      openSharedDialog(scope){
        this.shared_permission.permission=[];
        this.shared_permission.show =true;
        for (var i = 0; i < scope.row.permissions.length; i++) {
          this.shared_permission.permission.push(scope.row.permissions[i].id);
        }
      },
      openShareDialog(scope){
        this.permission.permission=[];
          this.permission.show =true;
          for (var i = 0; i < scope.row.permissions.length; i++) {
            this.permission.permission.push(scope.row.permissions[i].id);
          }
      }
    }
  }
</script>

<style>
</style>



