<template>
  <div style="margin-top: 5px;">
    <!-- 此处是面包屑 -->

    <div style="float: left;">

      <el-breadcrumb style="padding: 5px;" separator="/">
        <el-breadcrumb-item :to="{ path: '/storage' }">全部文件</el-breadcrumb-item>
        <el-breadcrumb-item v-if="type!='folder'" v-for="(item,index) in list" :to="{path:'/storage?parentName='+pathList[index]+''}">{{item}}</el-breadcrumb-item>
        <el-breadcrumb-item v-else v-for="(item,index) in list" :to="{path:'/storage/ShareFolder?parentName='+pathList[index]+''}">{{item}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <el-dialog title="" :visible.sync="perentDialog" width="30%" center>
      <p>{{fileName}}</p>
      <el-progress :percentage="perent" :format="format"></el-progress>
    </el-dialog>

    <div style="margin-bottom: 10px;float: right;margin-right: 1.25rem;display: -webkit-box;">
      <div>
        <uploader :options="options">
          <el-tooltip effect="dark" content="上传" placement="bottom">
            <uploader-btn><i class="el-icon-upload"></i></uploader-btn>
          </el-tooltip>
          <uploader-list style="z-index: 10000;position:fixed; bottom:0; right:0;border: #000000 1px;"></uploader-list>
        </uploader>
      </div>
      <el-tooltip effect="dark" content="新增文件夹" placement="bottom">
        <button @click="createFolder" style="margin-left: 10px;" class="createFolder"><i class="el-icon-folder-add"></i></button>
      </el-tooltip>
      <el-tooltip effect="dark" content="下载选中文件" placement="bottom">
        <button @click="download()" style="margin-left: 10px;" class="createFolder"><i class="el-icon-download"></i></button>
      </el-tooltip>
      <el-tooltip effect="dark" content="离线下载" placement="bottom">
        <button @click="outline_download()" style="margin-left: 10px;" class="createFolder"><i class="el-icon-paperclip"></i></button>
      </el-tooltip>
    </div>
  <el-dialog :visible.sync="downloadDialog.show" :before-close="beforeDestroy" width="600px" top="3vh">
    <div style="text-align: left;">
      <el-button size="small" plain @click="urldownload()">URL下载</el-button>
      <el-button size="small" plain>Torrnet下载</el-button>
    </div>
    <div>
      <table>
        <tr v-for="row in downloadDialog.total">
          <td style="margin-right: 20px;">{{row.name.substring(0,30)}}</td>
          <td style="margin-right: 20px;">{{parse(row.downloadSpeed)+"/S"}}</td>
          <td style="margin-right: 20px;">{{((row.completedLength/row.totalLength)*100).toFixed(2)+"%"}}</td>
        </tr>
      </table>
    </div>
  </el-dialog>
  </div>
</template>
<script>
  import eventHub from '../assets/EventBus.js'
  import {
    byteToSize,
    checkType
  } from '../../static/Utils.js'
  export default {
    inject: ['reload'],
    data() {
      return {
        userName: '',
        parentName: this.$route.query.parentName,
        perent: 0,
        form: {
          parentName: ''
        },
        list: [],
        pathList: [],
        imgUrl: require("../assets/logo_1.png"),
        perentDialog: false,
        fileName: '',
        options: {
          target: '/api/upload/uploadSlice',
          testChunks: true,
          query: {
            path: ''
          }
        },
        downloadDialog: {
          show: false,
          url: '',
          download_list: {
            stopped: [],
            waitting: [],
            active: []
          },
          total: []
        },
        timer: '',
        gid: [],
        history: {
          show: false,
          history: []
        }
      }
    },
    props: ["type"],
    computed: {
      // 这里定义上传文件时携带的参数，即表单数据
      upData: function() {
        return {
          body: this.form
        }
      }
    },
    mounted() {
      if (this.parentName == null || this.parentName == "undefined") {
        this.parentName = "/";
      }
      this.options.query.path = this.parentName;
      this.list = this.parentName.split("/").filter(item => item != '');
      this.pathList[0] = "/" + this.list[0] + "/";

      for (var i = 1; i < this.list.length; i++) {
        this.pathList[i] = this.pathList[i - 1] + this.list[i] + "/";
      }
    },
    methods: {
      history_upload() {

      },
      parse(bytes) {
        return byteToSize(bytes);
      },
      urldownload() {
        this.$prompt('URL链接', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(({
          value
        }) => {
          let form = new FormData();
          form.append("url", value);
          form.append("path", this.parentName);
          this.$axios.post("/api/download/addUri", form).then(response => {
            this.$message('添加成功');
          })
        })
      },
      beforeDestroy() {
        clearInterval(this.timer);
        this.downloadDialog.show = false;

      },
      outline_download() {
        this.downloadDialog.show = true;
        //设置定时器
        this.timer = setInterval(this.getData, 2500);
      },
      getData() {
        this.$axios.get("/api/download/active").then(response => {
          this.downloadDialog.total = response.data;
        })
      },
      download() {
        eventHub.$emit("Download");
      },
      format(percentage) {
        return "";
      },
      progress(event, file, fileList) {
        this.perent = file.percentage;
      },
      createFolder() {
        this.$prompt('请输入文件夹名', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /^((?!\\|\/|:|\*|\?|<|>|\||'|%).){1,8}$/,
          inputErrorMessage: '文件夹名格式不正确'
        }).then(({
          value
        }) => {
          var name;
          name = this.parentName;
          this.$axios.post('/api/disk/uploadFolder', {
            selfName: value,
            parentName: name
          }).then(responese => {
            console.log(responese)
            if (responese.data.code === 20000) {
              this.reload();
            }
          });
        });
      }
    }
  }
</script>

<style>
  .header button {
    text-align: left;
  }

  el-upload-list el-upload-list--text {
    position: fixed;
    right: 20px;
    bottom: 20px;
  }

  .createFolder {
    display: inline-block;
    position: relative;
    padding: 4px 8px;
    font-size: 100%;
    line-height: 1.4;
    color: #666;
    border: 1px solid #666;
    cursor: pointer;
    border-radius: 2px;
    background: none;
    outline: none;
  }
</style>



