<template>
  <el-container>
    <el-header v-if="this.type=='all'||typeof this.type==='undefined'||this.type=='folder'" style="height: 40px;margin: 0 0 0px 0;">
      <headers :type="type"></headers>
    </el-header>
    <el-main style="padding-left: 10px;padding-top: 0rem;">
      <el-table :data="tableData" fit @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55">
        </el-table-column>
        <el-table-column label="文件名" min-width="100%">
          <template slot-scope="scope" @mousemove="">
            <TableColumnName :scope="scope" :type="type"></TableColumnName>
          </template>
        </el-table-column>

        <el-table-column prop="size" label="大小" align="center" width="200px">
          <template slot-scope="scope">
            {{bytesToSize(scope.row)}}
          </template>
        </el-table-column>
        <el-table-column label="创建日期" fit align="center" width="300px" max-width="300px">
          <template slot="header" slot-scope="scope">
            <el-input @keyup.enter.native="s(search)" v-model="search" size="mini" placeholder="输入关键字搜索" />
          </template>
          <template slot-scope="scope" filter-method="filter()">
            <span value="">{{parse(scope.row.createDate)}}</span>
          </template>
        </el-table-column>
      </el-table>

      <el-dialog customClass="pdfDialog" size="full" :title="office_.fileName" :visible.sync="office_.officeDialog"
        :before-close="handleClose">
        <embed :src="office_.filePath" type="application/pdf" width="1000px" height="1000px" internalinstanceid="81" />
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="office_.officeDialog=false">确 定</el-button>
        </span>
      </el-dialog>

      <el-image-viewer v-if="showViewer" :on-close="close" :url-list="[pics]" />
      </el-drawer>
      <el-dialog :visible.sync="common_preview.common_preview_value" width="55%" top="3vh">
        <pre class='hljs' v-highlightjs="common_preview.common_preview_content"><code class="java"></code></pre>
      </el-dialog>

      <aplayer v-if="showMusic" autoplay mini float :music="Music_preview"></aplayer>
    </el-main>
  </el-container>
</template>

<script>
  import headers from "../components/header.vue"
  import footers from "../components/footer.vue"
  import item from "../components/ChoseItem.vue"
  import TableColumnName from "../components/TableColumnName.vue"
  import ElImageViewer from 'element-ui/packages/image/src/image-viewer'
  import aplayer from 'vue-aplayer'
  import CommonDialog from '../components/preview/common_preview.vue'
  import 'highlight.js/styles/atom-one-dark.css';

  import {
    byteToSize,
    checkType
  } from '../../static/Utils.js'
  import eventHub from '../assets/EventBus.js'

  export default {
    inject: ['reload'],
    components: {
      headers,
      footers,
      ElImageViewer,
      item,
      TableColumnName,
      CommonDialog,
      aplayer
    },
    data() {
      return {
        tableData: [],
        parentName: '/',
        path: "",
        userName: '',
        dialogVisible: false,
        name: "All",
        FolderName: '',
        search: '',
        showViewer: false,
        pics: '',
        office_: {
          officeDialog: false,
          officeFileName: '',
          officePath: ''
        },
        multipleSelection: [],
        common_preview: {
          common_preview_value: false,
          common_preview_content: ''
        },
        showMusic: false,
        Music_preview: {
          src: '',
          title: '',
          pic: require('../assets/test.gif')
        }
        

      }
    },
    watch: {
      '$route'(to, from) {
        this.$router.go(0);
      }
    },
    created() {
      eventHub.$on('Picture', this.picture);
      eventHub.$on('Music', this.Music);
      eventHub.$on('Torrent', this.torrent);
      eventHub.$on('Download', this.download);
      eventHub.$on('PDF', this.PDF);
      eventHub.$on('Common', this.Common);
    },
    props: ['type'],
    mounted() {
      if (typeof this.$route.query.parentName != "undefined") {
        this.parentName = this.$route.query.parentName
      }

      if (this.parentName == "/" && (this.type == "all" || this.type == "undefined")) {
        this.tableData.push({
          createDate: "2021-01-02T05:07:52.000+00:00",
          fid: "-1",
          isDir: 1,
          parentName: "/",
          selfName: "共享文件夹",
          size: 0,
          href: '/storage/ShareFolder'
        });
      }
      this.getList(this.path, this.parentName);
    },
    methods: {
      handleSelectionChange(val) {
        let array = [];
        for (var i = 0; i < val.length; i++) {
          array.push(val[i].fid);
        }
        this.multipleSelection = array;
      },

      download() {
        if (this.multipleSelection.length < 1) {
          return;
        } else if (this.multipleSelection == 1) {
        } else if (this.multipleSelection > 2) {
          let form = new FormData();
          form.append("fid", this.multipleSelection);

          var zipName = "批量下载" + this.multipleSelection.length + "个文件.zip";
          this.$axios.post("/api/disk/download", form, {
            headers: { // 这里需要使用form-data格式数据发送请求
              "Content-Type": "application/x-www-form-urlencoded"
            },
            responseType: "blob"
          }).then(responese => {

            console.log(responese);
            let blob = new Blob([responese.data], {
              type: 'application/zip'
            });
            if ('download' in document.createElement('a')) {

              let a = document.createElement('a');
              a.style.display = 'none';
              a.href = window.URL.createObjectURL(blob);
              a.download = zipName;
              a.click();

            } else {
              navigator.msSaveBlob(blob, zipName)
            }

          });
        }
      },

      getFile(row, testfuntion) {
        this.$axios.get('/api/disk/getFile', {
          params: {
            pathName: encodeURIComponent(row.parentName),
            fileName: encodeURIComponent(row.selfName)
          }
        }).then(responese1 => {
          if (responese1.data.code == 20000) {
            this.$axios.get("/api/" + responese1.data.result.path).then(resolve => {
              testfuntion("/api/" + responese1.data.result.path);
            }, reject => {
              this.open8(row.selfName + '还没准备好', 'error');
            });
          }
        });
      },


      close() {
        this.showViewer = false
      },

      parse(value) {
        return new Date(value).toLocaleString();
      },

      bytesToSize(row) {
       
        if (row.isDir == 1) {
          return "-";
        } else {
          return byteToSize(row.size);
        }
      },

      s(keyword) {
        this.$axios.get('/api/disk/searchFiles', {
          params: {
            keyword: keyword
          },
        }).then(responese => {
          this.tableData = [];
          this.tableData = responese.data.result;
        });
      },

      CreateFolder() {
        var name;
        if (this.parentName == "/" && this.parentName == this.path) {
          name = "/";
        } else {
          name = this.parentName + this.path + "/"
        }
        this.$axios.post('/api/disk/createFolder', {
          selfName: this.FolderName,
          parentName: name
        }).then(responese => {
          if (responese.data.code === 20000) {
            this.dialogVisible = false
            this.reload();
          }
        });
      },
      Music(row) {

        this.$axios.get("/api/Music/music", {
          params: {
            id: row.fid
          }
        }).then(response => {
          if (response.data.code === 20000) {
            console.log(this.Music_preview);
            this.Music_preview = response.data.result;
            this.showMusic = true;
            console.log(this.Music_preview);
          }
        });

      },
      torrent(row) {
        var vue = this;

        var msg = "确定要离线 " + row.selfName + "种子的内容吗";
        if (confirm(msg) == true) {
          this.aria2Torrent(row, function() {
            this.$notify({
              title: '成功',
              message: '添加成功!',
              type: 'success'
            });
          });
        }
        this.insertFresh();
      },
      //todo
      picture(row) {
        var vue = this;
        this.getFile(row, function(ex) {
          vue.showViewer = true;
          vue.pics = ex
        });
      },
      //todo
      office(row, vue) {
        this.getFile(row, function(ex) {
          vue.office_.officeDialog = true;
          vue.office_.fileName = row.selfName
          vue.office_.filePath = ex
        });
      },

      aria2Torrent(row, callback) {
        this.$axios.get('/api/disk/aria2Torrent', {
          params: {
            fid: row.fid
          }
        }).then(responese => {
          if (responese.data.code == 20000) {
            callback();
          }
        });
      },
      PDF(row) {

      },
      Common(row) {
        this.$axios.get("/api/disk/getFileById", {
          params: {
            fid: row.fid
          }
        }).then(response => {
          this.common_preview.common_preview_value = true;
          // this.common_preview.common_preview_content = response.data;
          this.$axios.get("/api/" + response.data.result.path).then(response1 => {
            this.common_preview.common_preview_content = response1.data;
          }).catch(response1 => {

          })
        }).catch(response => {

        })
      },
      getList(path, parentName) {

        if (this.type == 'all' || typeof this.type === 'undefined') {
          this.$axios.get('/api/disk/fileList', {
            params: {
              parentName: parentName
            }
          }).then(responese => {
            if (responese.data.code == 20000) {
              for (var i = 0; i < responese.data.result.length; i++) {
                this.tableData.push(responese.data.result[i]);
              }
            }
          });
        } else if (this.type == "folder" && this.parentName == "/") {
          this.$axios.get("/api/disk/folder").then(responese => {
            this.tableData = responese.data.result;
          });
        } else if (this.type == "folder" && typeof this.parentName != "/") {
          this.$axios.get('/api/disk/fileList', {
            params: {
              parentName: parentName
            }
          }).then(responese => {
            if (responese.data.code == 20000) {
              for (var i = 0; i < responese.data.result.length; i++) {
                this.tableData.push(responese.data.result[i]);
              }
            }
          });
        } else {
          this.$axios.get("/api/Type/" + this.type).then(responese => {
            if (responese.data.code == 20000) {
              this.tableData = responese.data.result;
            }
          });
        }
      },


      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
    }
  }
</script>

<!-- <script src="../../Utils.js"></script> -->
<style>
  a {
    text-decoration: none;
    color: black;
  }

  .el-upload {
    text-align: left !important;
  }

  .pdfDialog {
    width: 1200px;
    height: 1300px;
  }

  .tableBox {
    /deep/.el-table th>.cell {
      text-align: center;
    }

    /deep/.el-table .cell {
      text-align: center;
    }
  }

  pre {
    text-align: left;
  }
</style>
<link rel="stylesheet" href="//cdnjs.cloudflare.com/ajax/libs/highlight.js/9.9.0/styles/default.min.css">



