<template>
  <div style="height: 100%; width: 100%;">
    <el-tabs v-model="activeName">
      <el-tab-pane label="下载完成" name="下载完成">
        <el-table :data="stop">
          <el-table-column prop="name"></el-table-column>
          <el-table-column prop="进度"><template slot-scope="scope">{{scope.row.files[0].length/scope.row.files[0].completedLength}}</template></el-table-column>
          <!-- <el-table-column prop=""></el-table-column>
          <el-table-column prop=""></el-table-column> -->
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="等待下载" name="等待下载">
        <el-table :data="waited">
          <el-table-column label="下载名" prop="name"></el-table-column>
          <el-table-column label="进度"><template slot-scope="scope">{{scope.row.files[0].length/scope.row.files[0].completedLength}}</template></el-table-column>
          <!-- <el-table-column prop=""></el-table-column>
          <el-table-column prop=""></el-table-column> -->
        </el-table>
      </el-tab-pane>

    </el-tabs>
  </div>
</template>


<script>
  export default {
    data() {
      return {
        activeName: '下载完成',
        stop: [],
        waited: [],
      }
    },
    mounted() {
      this.getStop();
      this.getWait();
    },
    methods: {
      getStop() {
        this.$axios.get("/api/download/stop").then(response => {
          this.stop = (response.data.result);
        })
      },
      getWait() {
        this.$axios.get("/api/download/wait").then(response => {
          this.wait = response.data.result
        })
      }
    }
  }
</script>



