<template>
  <div>
    <bannerBar></bannerBar>
    <el-container>
      <sideList></sideList>
      <div style="width: 100%;">
        <div>
          <el-breadcrumb style="padding: 5px;" separator="/">
            <el-breadcrumb-item :to="{ path: '/storage/Oss' }">桶列表</el-breadcrumb-item>
            <el-breadcrumb-item v-if="BucketName!='undefined'&&BucketName!=null&&BucketName!=''" :to="{ path: '/storage/Oss?BucketName='+BucketName+'&pathName=%2F' }">全部文件</el-breadcrumb-item>
            <el-breadcrumb-item v-for="(item,index) in paths" :to="{path:'/storage/Oss?BucketName='+BucketName+'&pathName='+paths[index]+''}">{{item}}</el-breadcrumb-item>
          </el-breadcrumb>
          <el-button style="float: right;"  type="text" @click="dialog=true">添加Oss</el-button>
        </div>


        <el-table :data="OssFiles">
          <el-table-column label="文件名" min-width="500px"><template slot-scope="scope"><a @click="go(scope.row)"><span>{{scope.row.path}}</span></a></template></el-table-column>
          <el-table-column prop="size" label="大小"></el-table-column>
          <el-table-column prop="lastModifity" label="最后修改日期"></el-table-column>
        </el-table>
        <el-dialog :visible.sync="dialog" width="55%" top="3vh">
          <el-input style="margin-bottom: 10px;" v-model="OssData.SecretId" placeholder="SecretId"></el-input>
          <el-input style="margin-bottom: 10px;" placeholder="SecretKey" v-model="OssData.SecretKey" show-password></el-input>
          <el-input style="margin-bottom: 10px;" v-model="OssData.Region" placeholder="Region"></el-input>

          <el-divider></el-divider>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialog = false">取 消</el-button>
            <el-button type="primary" @click="add()">确 定</el-button>
          </span>
        </el-dialog>
      </div>
    </el-container>

  </div>
</template>

<script>
  import bannerBar from "../components/banner_bar.vue"
  import sideList from "../components/sideList.vue"
  export default {
    components: {
      sideList,
      bannerBar
    },
    data() {
      return {
        BucketName: '',
        pathName: '',
        OssFiles: [],
        paths: [],
        dialog: false,
        OssData: {
          Region: '',
          SecretId: '',
          SecretKey: ''
        }
      }
    },
    methods: {
      add() {

      },
      get() {
        if (typeof this.$route.query.BucketName != "undefined") {
          this.$axios.get("/api/Oss/OssFiles", {
            params: {
              buckName: encodeURIComponent(this.BucketName),
              path: this.pathName
            }
          }).then(response => {
            this.OssFiles = response.data.result;
            console.log(this.OssFiles);
          });
        } else {
          this.$axios.get("/api/Oss/Oss").then(response => {
            this.OssFiles = response.data.result;
          });
        }
      },
      add() {
        let form = new FormData();
        form.append("SecretId", this.OssData.SecretId);
        form.append("SecretKey", this.OssData.SecretKey);
        form.append("Region", this.OssData.Region);
        this.$axios.put("/api/Oss/Oss", form).then(response => {
          console.log(response);
        });
      },
      go(row) {
        var name = row.path;
        var url = "/storage/Oss";
        console.log(encodeURIComponent(name));
        if (typeof row.lastModifity != "undefined"&&row.lastModifity!=null) {
          console.log("test");
        }
        // 有三种情况，第一种情况是从bucket进去 此时无bucketName和path 第二种情况是有bucketName,无path 第三种情况是有bucketName和path
        else if (typeof this.$route.query.BucketName == "undefined" && typeof this.$route.query.pathName == "undefined") {
          this.$router.push({
            path: url,
            query: {
              BucketName: encodeURIComponent(name),
              pathName: "/"
            }
          });
        } else if (typeof this.$route.query.BucketName != "undefined" && typeof this.$route.query.pathName ==
          "undefined") {
          this.$router.push({
            path: url,
            query: {
              BucketName: encodeURIComponent(this.BucketName),
              pathName: "/"
            }
          });
        } else if (typeof this.$route.query.BucketName != "undefined" && typeof this.$route.query.pathName !=
          "undefined") {
          var path = "";
          if (this.pathName == "/") {
            path = "/" + name
          } else {
            path = "/" + this.pathName + name
          }
          this.$router.push({
            path: url,
            query: {
              BucketName: encodeURIComponent(this.BucketName),
              pathName: path
            }
          });
        }
      }
    },
    mounted() {
      if (typeof this.$route.query.BucketName != "undefined") {
        this.BucketName = this.$route.query.BucketName
      }
      if (typeof this.$route.query.pathName != "undefined") {
        this.pathName = this.$route.query.pathName
      }

      this.paths = this.pathName.split("/").filter(item => item != '');
      for (var i = 1; i < this.paths.length; i++) {
        this.paths[i] = "/" + this.paths[i - 1] + this.paths[i] + "/";
      }

      this.get();
    },
    watch: {
      $route(to, from) {
        this.$router.go(0)
      }
    }


  }
</script>

<style>
</style>



