<template>
  <div>
    <img :src="chooseIcon(scope.row)" style="vertical-align: middle;" />
    <a style="color: #000000;margin-left: 5px;" @click="getDetails(scope.row)">{{scope.row.selfName}}</a>
    <item :scope="scope"></item>
  </div>
</template>

<script>
  import {
    checkType,
    checkWithType
  } from '../../static/Utils.js'

  import eventHub from '../assets/EventBus.js'
  import item from "../components/ChoseItem.vue"

  export default {
    components: {
      item
    },
    data() {
      return {

      }
    },
    methods: {
      getDetails(row) {
        if (row.isDir == 1) {
          //跳转
          //检查是不是共享的文件夹
          if (typeof row.href == "undefined" || row.href == null) {
            var path = "";
            if (row.parentName == '/') {
              path = "/" + row.selfName + "/";
            } else {
              path = row.parentName + row.selfName + "/"
            }
            var url = '/storage';
            if (this.type == "folder") {
              url = url + '/ShareFolder';
              this.$router.push({
                path: url,
                query: {
                  parentName: path
                }
              });
            } else {
              this.$router.push({
                path: url,
                query: {
                  parentName: path,
                }
              });

            }
          } else {
            this.$router.push({
              path: row.href,
            });
          }


        } else {
          switch ((row.type)) {
            case "Video":
              this.video(row);
              break; //跳转
            default:
              eventHub.$emit(row.type, row);
              break;
              // case "Music":eventHub.$emit('music', row)
              //   break; //音乐 全局总线
              // case "Torrent":
              //   eventHub.$emit('torrent', row);
              //   break; //种子
              // case "Picture":
              //   eventHub.$emit('picture', row)
              //   break; //打开图片
              // case "Common":
              //   eventHub.$emit(row.type, row);
              //   break;
          }
        }
      },


      chooseIcon(row) {
        if (row.isDir == 1) {
          return require("../assets/icon/Folder.png");
        } else {
          switch (row.type) {
            case "Torrent":
              return require("../assets/icon/BT.png");
              break;
            case "Picture":
              return require("../assets/icon/Picture.png");
              break;
            case "Music":
              return require("../assets/icon/Music.png");
              break;
            case "Video":
              return require("../assets/icon/Video.png");
              break;
            case "Office":
              return require("../assets/icon/Text.png");
              break;
            case "PDF":
              return require("../assets/icon/PDF.png");
              break;
            case "Common":
              return require("../assets/icon/Text.png");
              break;
            case "Software":
              return require("../assets/icon/Software.png");
              break;
          }
          return require("../assets/icon/Other.png")
        }
      },


      close() {
        this.showViewer = false
      },

      s(keyword) {
        this.$axios.get('/api/disk/searchFiles', {
          params: {
            keyword: keyword
          },
        }).then(responese => {
          this.tableData = [];
          this.tableData = responese.data.result;
        });
      },

      download(index, row) {
        this.getFile(row, function(ex) {
          window.open(ex, '_blank')
        });
      },

      video(row) {
        let routeData = this.$router.resolve({
          path: '/video',
          query: {
            file: row.parentName + row.selfName
          }
        });
        window.open(routeData.href, '_blank');
      },
      //todo
      office(row, vue) {
        this.getFile(row, function(ex) {
          vue.office_.officeDialog = true;
          vue.office_.fileName = row.selfName
          vue.office_.filePath = ex
        });
      },

      aria2Torrent(row, callback) {
        this.$axios.get('/api/disk/aria2Torrent', {
          params: {
            fid: row.fid
          }
        }).then(responese => {
          if (responese.data.code == 20000) {
            callback();
          }
        });
      }
    },
    props: ["scope", "type"],
    mounted() {

    }
  }
</script>

<style>
</style>



