<template>
  <div style="float: right;">
    <el-dropdown size='mini'>
      <el-link :underline="false" icon="el-icon-more"></el-link>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item>
          <el-button type="text" v-if="check(scope.row.isDir,scope.row.selfName,0)" size="mini" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </el-dropdown-item>
        <el-dropdown-item>
          <el-button type="text" v-if="check(scope.row.type,scope.row.selfName,1)" size="mini" @click="DLNA(scope.row.fid)">投屏</el-button>
        </el-dropdown-item>
        <el-dropdown-item>
          <el-button size="mini" v-if="check(scope.row.type,scope.row.selfName,2)" type="text" @click="FileVersion( scope.row)">版本控制</el-button>
        </el-dropdown-item>
        <el-dropdown-item>
          <el-button size="mini" v-if="check(scope.row.type,scope.row.selfName,3)" type="text" @click="shareForm(scope.row)">共享</el-button>
        </el-dropdown-item>
        <el-dropdown-item>
          <el-button size="mini" v-if="check(scope.row.type,scope.row.selfName,4)" @click="rename(scope.row)" type="text">重命名</el-button>
        </el-dropdown-item>
        <el-dropdown-item>
          <el-button size="mini" v-if="check(scope.row.type,scope.row.selfName,5)" @click="move(scope.row)" type="text">移动到</el-button>
        </el-dropdown-item>
        <el-dropdown-item>
          <el-button size="mini" v-if="check(scope.row.type,scope.row.selfName,6)" @click="addCollect(scope.row)" type="text">添加到歌单</el-button>
        </el-dropdown-item>
        <el-dropdown-item>
          <el-button size="mini" v-if="check(scope.row.type,scope.row.selfName,7)" @click="addCollect(scope.row)" type="text">其余信息</el-button>
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-dialog :visible.sync="share_.show" width="55%" top="3vh">
      {{share_.fid}}
      <el-form ref="share_" :model="share_" label-width="80px">
        <el-form-item label="用户邮箱">
          <el-input v-model="share_.email"></el-input>
        </el-form-item>
        <el-form-item label="权限">
          <!-- <el-select v-model="share_.folderPermission" placeholder="请选择活动区域">
            <el-option v-for="folder in folderPermission" :label="folder.permissionName" :value="folder.permissionName"></el-option>
          </el-select> -->

          <el-checkbox v-for="folder in folderPermission" v-model="share_.folderPermission" :label="folder.id" border>{{folder.permissionName}}</el-checkbox>

        </el-form-item>
      </el-form><span slot="footer" class="dialog-footer">
        <el-button @click="share_.show = false">取 消</el-button>
        <el-button type="primary" @click="share()">确 定</el-button>
      </span>
    </el-dialog>
  </div>

</template>

<script>
  import {
    byteToSize,
    checkType
  } from '../../static/Utils.js'

  export default {
    data() {
      return {
        share_: {
          show: false,
          fid: '',
          email: '',
          folderPermission: []
        },
        folderPermission: []
      }
    },
    mounted() {
      this.$axios.get("/api/disk/folderPermissions").then(response => {
        this.folderPermission = response.data.result;
      }).catch(response => {
        console.log(response)
      })
    },
    methods: {
      check(type, name, index) {
        var video = [1, 1, 0, 0, 1, 1, 0, 1];
        var folder = [1, 0, 0, 1, 0, 0, 0, 1];
        var music = [1, 1, 0, 0, 1, 1, 1, 1];
        var Other = [1, 0, 0, 0, 1, 1, 0, 1];
        if (type == "Folder") {
          return folder[index] == 1;
        }
        switch (type) {
          case "Video":
            return video[index] == 1;
            break;
          case "Music":
            return music[index] == 1;
            break;
        }
        return Other[index] == 1;
      },
      shareForm(row) {
        this.share_.show = true;
        this.share_.fid = row.fid;
        this.$axios.get("/api/disk/folderPermissions").then(response => {
          this.folderPermission = (response.data.result);
        }).catch(response => {
          console.log(response)
        })
      },
      rename(row) {
        this.$prompt('请输入名称', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(({
          value
        }) => {
          let form = new FormData();
          form.append("targetName", value);
          this.$axios.put('/api/disk/Rename/' + row.fid, form).then(responese => {
            if (responese.data.code === 20000) {
              this.reload();
            }
          });
        });
      },

      share() {
        let form = new FormData();
        form.append("fid", this.share_.fid);
        form.append("email", this.share_.email);
        form.append("folderPermission", this.share_.folderPermission);
        this.$axios.put('/api/disk/folder', form).then(responese => {
          if (responese.data.code === 20000) {
            this.reload();
          }
        });
      },

      FileVersion(row) {
        this.$axios.post('/api/v1/FilesVersion/insertFilesVersionControl', {
          fid: row.fid + ''
        }).then(responese => {
          if (responese.data.code == 20000) {
            this.$notify({
              title: '成功',
              message: '添加成功!',
              type: 'success'
            });
          }
        });
      },

      DLNA(fid) {
        this.$prompt('设备名', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({
          value
        }) => {
          this.$axios.get('/api/Files/Share/DLNA', {
            params: {
              fid: fid,
              device: encodeURIComponent(value)
            }
          }).then(responese => {
            console.log(responese);
          })
        });
      },


      handleDelete(index, row) {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let form = new FormData();
          var arr = new Array();
          arr[0] = row.fid;
          form.append("id", arr);
          this.$axios.put('/api/Deletes/Delete', form).then(responese => {
            if (responese.data.code == 20000) {
              this.$notify({
                title: '成功',
                message: '删除成功!',
                type: 'success'
              });
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      move(row) {

      },

      ChooseCollectToAdd(row) {

      },
      addCollect(row) {

      }
    },
    mounted() {},
    props: ['scope'],
  }
</script>

<style>
</style>



